(function() {
    this.Tools = (function() {
        function Tools() {}

        Tools.prototype.getUrlParameterByName = function(name) {
            var regex, results;
            name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
            regex = new RegExp("[\\?&]" + name + "=([^&#]*)");
            var searchPath = location.search;
            if(searchPath !==undefined && searchPath !==null){
              if(searchPath ===''){
                searchPath = location.href.substring(location.href.indexOf('?'));
              }
            }
            results = regex.exec(searchPath);
            if (results) {
                return decodeURIComponent(results[1].replace(/\+/g, " "));
            } else {
                return "";
            }
        };

        Tools.prototype.parseUrl = function(url) {
            var reURLInformation;
            reURLInformation = new RegExp(['^(https?:)//', '(([^:/?#]*)(?::([0-9]+))?)', '(/[^?#]*)', '(\\?[^#]*|)', '(#.*|)$'].join(''));
            return reURLInformation.exec(url);
        };

        Tools.prototype.getPath = function() {
            return this.parseUrl(Meteor.absoluteUrl())[5];
        };

        return Tools;

    })();

}).call(this);
